import React from 'react';
// ! SWIPER
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination } from 'swiper';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';


const AboutPage = (props) => {
    
    let products = props.products
    return (
        <>
            <div className='container-fluid block abt-second'>
            <div className="container">
                <h4>Some tips</h4>
                    <Swiper
                        modules={[Navigation, Pagination]}
                        spaceBetween={50}
                        slidesPerView={3}
                        navigation
                        pagination={{ clickable: true }}
                >

                    {props.products.map(el => (
                        <SwiperSlide key={el.id}>
                            <img src={'https://picsum.photos/id/' + (el.id + 45) + '/370/240'} alt="" />
                            {/* <img src={'./img/' + el.img} alt="" /> */}
                            <div className='slide-body'>
                                <h5>Lorem ipsum dolor sit, amet consectetur adipisicing elit.</h5>
                                <small>10 jan 2020</small>
                            </div>
                    </SwiperSlide>
                    ))}

                </Swiper>
            </div>
            </div>

            <div className="container-fluid block abt-third">
                <div className="container">
                    <div className="banner">
                        <h3>Why do we help with legalization?</h3>
                        <p>We are here for UMKM in Indonesia to carry out the legalization process, which is sometimes complicated.</p>
                    </div>
                    <div className="row">
                        <div className="col-6">
                           <div className='inner'>
                             <h6>Information and Technology</h6>
                             <small>We provide a complete range of services for the continuity of your business activities.</small>
                           </div>
                        </div>
                        <div className="col-6">
                            <div className='inner'>
                                 <h6>Enviromental Law</h6>
                                 <small>We provide a complete range of services for the continuity of your business activities.</small>
                            </div>
                        </div>
                        <div className="col-6">
                            <div className='inner'> 
                                 <h6>Corporate and Commercial</h6>
                                 <small>We provide a complete range of services for the continuity of your business activities.</small>
                            </div>
                        </div>
                        <div className="col-6">
                           <div className='inner'>
                             <h6>Other Services</h6>
                             <small>We provide a complete range of services for the continuity of your business activities.</small>
                           </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
    
}

export default AboutPage;
