import React, { Component } from 'react';
import Card from './Card';

class Products extends Component {
    
    render() {
        // let row = [];
        // for(let i = 0;i<8;i++){
        //     row.push(<Card />)
        // }
        return (
            <>
                <h4>Our products</h4>
                <div class="row">
                   
                {
                    this.props.products.map(el =>(
                        <Card product={el} key={el.id} showModal={this.props.showModal} onAdd={this.props.onAdd}></Card>
                    ))
                }
                </div>
            </>
        );
    }
}

export default Products;
