import React, { Component } from 'react';
import Products from './Products';

class SecondBlock extends Component {


    render() {
        return (
            <div class='container-fluid second block'>
                <div class="container">
                    <div class="row">
                        <div class="col">
                            <div class="line">
                                <div>
                                    <img src="./img/award.png" alt="" />
                                </div>
                                <div><h6>
                                    High Quality.</h6><small>Crafted from top materials.</small>
                                </div>
                        </div>
                        </div>
                        <div class="col">
                            <div class="line"><div><img src="./img/medal.png" alt="" /></div><div><h6>Warrany Protection.</h6><small>Over 2 years.</small></div></div>
                        </div>
                        <div class="col">
                            <div class="line"><div><img src="./img/order1.png" alt="" /></div><div><h6>Free Shipping.</h6><small>Order over 150 $.</small></div></div>
                        </div>
                        <div class="col">
                            <div class="line"><div><img src="./img/support.png" alt="" /></div><div><h6>24 / 7 Support.</h6><small>Dedicated support.</small></div></div>
                        </div>
                    </div>


{
                    // ! PRODUCTS
}
                    <Products products={this.props.products} showModal={this.props.showModal} onAdd={this.props.onAdd} />
                </div>
            </div>
        );
    }
}

export default SecondBlock;
