import React, { Component } from 'react';

class ModalItem extends Component {
    render() {
        let item = this.props.item
        return (
            <div className='container-fluid block-modal'>
                <div className="modal-body">
                    <img src={'./img/' + item.img} alt="" />
                    <h4>{item.title}</h4>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Optio repellendus nisi unde ratione natus! Veniam illo adipisci aspernatur alias similique?</p>
                    {/* <button onClick={() => this.props.onAdd(item)}>Add to cart</button> */}
                    <button className='danger mb-4' onClick={() => this.props.closeModal()}>Close</button>
                </div>
            </div>
        );
    }
}

export default ModalItem;
