import React, { Component } from 'react';
import Navbar from './Navbar';
import { render } from '@testing-library/react';
import { Link } from 'react-router-dom';



class FirstBlock extends Component {
    
    
    render() {

        return (
            <div class='container-fluid first block'>
                <div class='container'>
                    <Navbar cart={this.props.cart} onDelete={this.props.onDelete} />

                        <div class="content">
         
                            {/* <div className='container-fluid block'>
                              {prods.map(el => ( <h3 key>{el.title}</h3>))}
                            </div> */}

                            
                            <div class="banner">
                                <h2>High-Quality
                                    Furniture Just <br />
                                    For You</h2>
                                <p>Our furniture is made from selected and best quality materials that are suitable for your dream home</p>
                                <Link to='/products' className='not-link'><button>Shop Now</button></Link>
                            </div>
                
                        <div class="img">
                            <img src="./img/img1.png" alt="" />
                            <p class="mt-4">Lorem ipsum dolor sit amet.</p>
                            <small>Lorem ipsum dolor sit amet consectetur adipisicing elit.</small>
                        </div>

                        </div>




                </div>
            </div>
        );
    }
}

export default FirstBlock;
