import { render } from '@testing-library/react';
// import '../node_modules/bootstrap/scss/bootstrap.scss';
import './dist/style.css';
import './components/FirstBlock';
import FirstBlock from './components/FirstBlock';
import SecondBlock from './components/SecondBlock';
import ModalItem from './components/ModalItem';
import Footer from './components/Footer';
import React, { Component } from 'react';

import { Routes, Route, Link } from 'react-router-dom';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ProductsPage from './pages/ProductsPage';
import NotFoundPage from './pages/NotFoundPage';

class App extends Component{
  
  constructor(props){
    super(props)
    this.state = {
      cart: [],
      products: [
        {
          id:1, 
          title: 'Syltherine',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod1.png'
        },
        {
          id:2, 
          title: 'Respira',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod2.png'
        },
        {
          id:3, 
          title: 'Pingky',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod3.png'
        },
        {
          id:4, 
          title: 'Syltherine',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod4.png'
        },
        {
          id:5, 
          title: 'Grifo',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod5.png'
        },
        {
          id:6, 
          title: 'Muggo',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod6.png'
        },
        {
          id:7, 
          title: 'Lorem',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod7.png'
        },
        {
          id:8, 
          title: 'Syltherine',
          text: 'Stylish cafe chair',
          price: 'Rp 2.500.000',
          img: 'prod8.png'
        },
      ],
      modal: false,
      modalItem: {
        id:1, 
        title: 'Syltherine',
        text: 'Stylish cafe chair',
        price: 'Rp 2.500.000',
        img: 'prod1.png'
      }
    }
    this.addToCart = this.addToCart.bind(this)
    this.deleteFromCart = this.deleteFromCart.bind(this)
    this.showModal = this.showModal.bind(this)
  }
  
   addToCart(prod) {
    let cancel = false
    this.state.cart.forEach(el => {
      if(el.id === prod.id) cancel = true
    })
    if(cancel) return
    this.setState({cart: [...this.state.cart, prod]})
  }
  deleteFromCart(id){
    this.setState({cart: this.state.cart.filter(el => el.id !== id)})
  }
  showModal(item = {}){
    this.setState({modalItem: item, modal: !this.state.modal})
  }

  render(){
      return (
        <div className="App wrapper">
          <FirstBlock cart={this.state.cart} onDelete={this.deleteFromCart} />
          {/* <SecondBlock products={this.state.products} onAdd={this.addToCart} showModal={this.showModal} />
          {this.state.modal && <ModalItem item={this.state.modalItem} onAdd={this.addToCart} closeModal={this.showModal} />} */}
            <Routes>


              <Route path='/funiro' element={
              
              <HomePage products={this.state.products} onAdd={this.addToCart} showModal={this.showModal} item={this.state.modalItem} closeModal={this.showModal} modal={this.state.modal} />
            
            } />


              <Route path='/funiro/about' element={<AboutPage products={this.state.products} />} />
              <Route path='/funiro/products' element={<ProductsPage />} />
              <Route path='/funiro/*' element={<NotFoundPage />} />
            </Routes>
          <Footer />

          
        </div>
      );
  }

}

export default App;
