import React, { useState } from 'react';
import { Link } from 'react-router-dom';

export default function Navbar(props) {

    let [isOpen, setOpen] = useState(false);
    let cart = props.cart;
        return (
            <nav class="navbar navbar-expand-lg">
                        <div class="container-fluid">
                            <a class="navbar-brand" href="/funiro">Funiro.</a>
                            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span class="navbar-toggler-icon">X</span>
                            </button>
                            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                                {/* <li class="nav-item">
                                    <a class="nav-link active" aria-current="page" href="/">Home</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link" href="/about">About</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link" href="/products">Products</a>
                                </li> */}
                                <li class="nav-item">
                                    <Link class="nav-link active" aria-current="page" to="/funiro">Home</Link>
                                </li>
                                <li class="nav-item">
                                    <Link class="nav-link" to="/funiro/about">About</Link>
                                </li>
                                <li class="nav-item">
                                    <Link class="nav-link" to="/funiro/products">Products</Link>
                                </li>
                                </ul>
                                <form class="d-flex">
                                <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                                </form>
                                <div class="line">
                                    <img src="./img/cart.png" onClick={() => setOpen(isOpen = !isOpen)} alt="cart" className={isOpen ? 'cart active' : 'cart'} />
                                    <img src="./img/pfp.png" alt="pfp" />

{
                                          // ! CART
}

                                    <div className={isOpen ? 'cart-box cartOpened' : 'cart-box'}>
                                        {/* <div className='cart-item'>
                                            <img src="img/prod3.png" alt="" />
                                            <div><h6>lorem</h6><small>$49,5</small></div>
                                        </div> */}
                                        {
                                        
                                        
                                        cart.length > 0 
                                            ?    cart.map(el => (
                                                    <div key={el.id} className='cart-item'>
                                                    <img src={`img/${el.img}`} alt="" />
                                                    <div><h6>{el.title}</h6><small>{el.price}</small></div>
                                                    <img src="img/trash.png" alt="" className='delete' onClick={() => props.onDelete(el.id)} />
                                                </div>
                                                ))
                                            : "    "
                                        
                                        
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </nav>
        );
}

// export default Navbar;
