import React, { Component } from 'react';

class Card extends Component {
  

    render() {
        let prod = this.props.product
        return (
            <div class="col-3">
                <div class="inner">
                  <div class="card">
                    <img src={'./img/' + prod.img} class="card-img-top" alt="..." />
                    <div class="card-body">
                      <h5 class="card-title">{prod.title}</h5>
                      <small class="card-text">{prod.text}</small>
                      <h6>{prod.price} </h6>
                    </div>
                  </div>
                  <div class="add">
                    <button class="empty" onClick={() => this.props.onAdd(prod)}>Add to cart</button>
                    
                    <div>
                      <div class="line">
                        <span><img src="./img/share2.png" alt="" />Share</span>
                        <span><img src="./img/like.png" alt="" />Like</span>
                      </div>
                    </div>

                    <button className='mt-4' onClick={() => this.props.showModal(prod)}>Show Info</button>

                  </div>
                </div>
              </div>
         
        );
    }
}

export default Card;
